<template>
    <div :class="darkmode ? 'dark-card' : 'card'">
        <div class="card-header-flex">
            <div class="card-header-text">
                <i class="fas fa-coins"></i>
                Means of Payment
            </div>
        </div>

        <div class="card-body">
            <div
                class="table-responsive-xl w-100"
                v-if="content != undefined && content.length > 0"
            >
                <table
                    :class="[
                        'responsive-table-hover',
                        darkmode ? 'table-darkmode' : 'table-lightmode'
                    ]"
                >
                    <TableHeader :cells="headerCells"></TableHeader>

                    <tbody>
                        <TableBody
                            v-for="(item, index) in content"
                            :key="index"
                            :content="item"
                            :index="index"
                            @delete="$emit('data-delete', index)"
                            @update="updateData"
                        ></TableBody>
                    </tbody>
                </table>
            </div>

            <EmptyPlaceholder v-else></EmptyPlaceholder>

            <hr>

            <div class="row">
                <div class="col-md-4">
                    <button
                        class="big-button-primary"
                        :disabled="!buttonsEnabled"
                        @click="$emit('data-add')"
                    >
                        New mean of payment
                    </button>
                </div>
                <div class="col-md-4 my-2 my-md-0">
                    <button
                        class="big-button-danger"
                        :disabled="!buttonsEnabled"
                        @click="$emit('data-reset')"
                    >
                        Reset changes
                    </button>
                </div>

                <div class="col-md-4">
                    <button
                        class="big-button-success"
                        :disabled="!buttonsEnabled"
                        v-html="saveButton"
                        @click="dataSave"
                    >
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import TableHeader from "../../TableHeader.vue";
import TableBody from "./MeansOfPaymentTableContent.vue"
import EmptyPlaceholder from "../../EmptyPlaceholder.vue";

export default {
    components: {
        TableHeader,
        TableBody,
        EmptyPlaceholder
    },
    props: {
        darkmode: Boolean,
        buttonsEnabled: Boolean,
        content: Array
    },
    data() {
        return {
            headerCells: [
                {
                    text: "Name",
                    tooltip: "The name of your mean of payment"
                },
                {
                    text: "Income",
                    tooltip: "Use in income"
                },
                {
                    text: "Outcome",
                    tooltip: "Use in outcome"
                },
                {
                    text: "Charts",
                    tooltip: "Display on charts"
                },
                {
                    text: "Summary",
                    tooltip: "Count to summary"
                },
                {
                    text: "First entry",
                    tooltip: "Date from which to add the starting balance"
                },
                {
                    text: "Starting balance",
                    tooltip: "Amount which to use for the first entry"
                },
                {}
            ],
            saveButton: 'Save changes'
        }
    },
    methods: {
        dataSave() {
            this.saveButton = '<span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>'
            this.$emit('data-save');
        },
        updateData() {
            this.$emit("data-change")
        }
    }
}
</script>
